@-webkit-keyframes fade-out {
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}

@keyframes fade-out {
    from{
        opacity: 1;
    }
    to{
        opacity: 0;
    }
}

@-webkit-keyframes fade-in {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

@keyframes fade-in {
    from{
        opacity: 0;
    }
    to{
        opacity: 1;
    }
}

.fade-in {
    -webkit-animation: fade-in 1.5s infinite;
            animation: fade-in 1.5s infinite;
}

.fade-out {
    -webkit-animation: fade-out 1.5s infinite;
            animation: fade-out 1.5s infinite;
}

h1 {
    margin: 20%;
}


